function slider({
  container,
  slide,
  nextArrow,
  prevArrow,
  totalCounter,
  currentCounter,
  wrapper,
  field,
}) {
  const slider = document.querySelector(slide);
  const slides = document.querySelectorAll(container);
  const prev = document.querySelector(prevArrow);
  const next = document.querySelector(nextArrow);
  const total = document.querySelector(totalCounter);
  const current = document.querySelector(currentCounter);
  const slidesWrapper = document.querySelector(wrapper);
  const slidesFild = document.querySelector(field);
  const width = window.getComputedStyle(slidesWrapper).width;

  let slideIndex = 1;
  let offset = 0;

  function currentSlideNumber() {
    if (slides.length < 10) {
      current.textContent = `0${slideIndex}`;
    } else {
      current.textContent = slideIndex;
    }
  }

  if (slides.length < 10) {
    total.textContent = `0${slides.length}`;
  } else {
    total.textContent = slides.length;
  }

  currentSlideNumber();

  slidesFild.style.width = 100 * slides.length + "%";
  slidesFild.style.display = "flex";
  slidesFild.style.transition = "0.5s all";
  slidesWrapper.style.overflow = "hidden";
  slides.forEach((slide) => {
    slide.style.width = width;
  });

  slider.style.position = "relative";

  const indecators = document.createElement("ol");
  const dots = [];
  indecators.classList.add("carousel-indicators");
  slider.append(indecators);

  for (let i = 0; i < slides.length; i++) {
    const dot = document.createElement("li");
    dot.setAttribute("data-slide-to", i + 1);
    dot.classList.add("dot");
    if (i === 0) {
      dot.style.opacity = 1;
    }
    indecators.append(dot);
    dots.push(dot);
  }

  function dotsActive() {
    dots.forEach((dot) => (dot.style.opacity = 0.5));
    dots[slideIndex - 1].style.opacity = 1;
  }

  function fromStringToNumberDeleteLetters(string) {
    return +string.replace(/\D/g, "");
  }

  next.addEventListener("click", () => {
    if (
      offset ===
      fromStringToNumberDeleteLetters(width) * (slides.length - 1)
    ) {
      offset = 0;
      slideIndex = 1;
    } else {
      offset += fromStringToNumberDeleteLetters(width);
      slideIndex++;
    }
    slidesFild.style.transform = `translateX(-${offset}px)`;

    currentSlideNumber();
    dotsActive();
  });

  prev.addEventListener("click", () => {
    if (offset === 0) {
      offset = fromStringToNumberDeleteLetters(width) * (slides.length - 1);
      slideIndex = slides.length;
    } else {
      offset -= fromStringToNumberDeleteLetters(width);
      slideIndex--;
    }
    slidesFild.style.transform = `translateX(-${offset}px)`;

    currentSlideNumber();
    dotsActive();
  });

  dots.forEach((dot) => {
    dot.addEventListener("click", (event) => {
      const slideTo = event.target.getAttribute("data-slide-to");

      slideIndex = slideTo;
      offset = fromStringToNumberDeleteLetters(width) * (slideTo - 1);
      slidesFild.style.transform = `translateX(-${offset}px)`;

      currentSlideNumber();
      dotsActive();
    });
  });
  // function showSlides(n) {
  //   if (n > slides.length) {
  //     slideIndex = 1;
  //   }

  //   if (n < 1) {
  //     slideIndex = slides.length;
  //   }

  //   slides.forEach((item) => item.classList.add("hide"));
  //   slides[slideIndex - 1].classList.remove("hide");

  //   if (slides.length < 10) {
  //     current.textContent = `0${slideIndex}`;
  //   } else {
  //     current.textContent = slideIndex;
  //   }
  // }

  // showSlides(slideIndex);

  // if (slides.length < 10) {
  //   total.textContent = `0${slides.length}`;
  // } else {
  //   total.textContent = slides.length;
  // }

  // function plusSlides(n) {
  //   showSlides((slideIndex += n));
  // }

  // prev.addEventListener("click", () => {
  //   plusSlides(-1);
  // });

  // next.addEventListener("click", () => {
  //   plusSlides(1);
  // });
}

export default slider;
